#maindiv{
    width: 100%;
    height: 100vh;
}
#navbaring{
    padding:  0% 3%;
    background:white;
    display: flex;
    justify-content: space-between;
    align-self: center;
    border-bottom: 3px solid black;
}
#desdiv{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 20%;
}
#homecoming{
    color: maroon;
    font-size: 3rem
}
#contentDiv{
    padding-top: 2%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    background-color: whitesmoke;
}
.ml{margin-left: 10px;}
#manageInventory,#manageOrders,#ManageTeam{
    box-shadow: 0px 0px 8px black;
    width:30%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background-color: white;
    border-radius: 30px;
    

}
#inventory{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}
#Team{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
}
#TeamDetails{
    padding-top: 5%;
    width: 50%;
    display: flex;
    flex-direction: column ;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 0px 0px 5px black;
    background-color: whitesmoke;
    font-size: 1rem;
    border-radius: 20px;
    margin-top: 2%;
}
#orders{
    width: 100%;
    display: flex;
    justify-content: center;
  
    align-items: center;
    flex-wrap: wrap;
}
#orderdetails{
    width: 80%;
    padding: 4%  6%;    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 0px 0px 5px black;
    border-radius: 20px;
    background-color: whitesmoke;
}
.buttonClass {
    font-size:15px;
    font-family:Arial;
    width:140px;
    height:50px;
    border-width:1px;
    color:#3a8a9e;
    border-color:#d6bcd6;
    font-weight:bold;
    border-radius:15px;
    box-shadow: 3px 4px 0px 0px #899599;
    text-shadow: 0px 1px 0px #e1e2ed;
    background:linear-gradient(#ededed, #bab1ba);
    text-align: center;
    padding: unset;
  }
  
  .buttonClass:hover {
    background: linear-gradient(#bab1ba, #ededed);
  }
  #meddetails{
    width:40%;
    box-shadow: 0px 0px 8px black;
    background-color: whitesmoke;
    margin: 2% 0;
    text-align: center;
    border-radius: 30px;
  }
  #logouting{
      width: 70px;
      height: 25px;
      border-radius: 25px;
      outline: none;
      border: none;
      font-size: 0.8rem;
      background-color: whitesmoke;
      align-items: center;
      box-shadow: 0 0 5px black;
      position: absolute;
      left:82%;
      top: 11%;
      font-weight: 600;
  }