#maindiv {
    width: 100%;
    margin-top: unset;
  }
  #crudordnav {
    padding: 0% 3%;
    background: whitesmoke;
    display: flex;
    justify-content: space-between;
    align-self: center;
    box-shadow: 2px 2px 5px black;
  }
  #desdiv {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 20%;
  }
  #homecoming {
    color: maroon;
    font-size: 3rem;
  }
  #formDiv{
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      padding: 5%;
      width: 40%;
      margin: 5% auto;
      box-shadow:0 0 5px black;
  }
  form{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 60vh;
    width: 60%;
  }
#stock,#total{
    width: 80%;
    height: 7vh;
    font-size: 2rem;
    border: none;
    outline: none;
    border-bottom: 3px solid black;
    background-color: transparent;
    }

  #buttonDiving{
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      margin: unset;
      margin-top: 5%;
  }
  .buttonClass7{
    font-size:15px;
    font-family:Arial;
    width:140px;
    height:50px;
    border-width:1px;
    color:#3a8a9e;
    border-color:#d6bcd6;
    font-weight:bold;
    border-top-left-radius:15px;
    border-top-right-radius:15px;
    border-bottom-left-radius:15px;
    border-bottom-right-radius:15px;
    box-shadow: 3px 4px 0px 0px #899599;
    text-shadow: 0px 1px 0px #e1e2ed;
    background:linear-gradient(#ededed, #bab1ba);
    padding: unset;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .buttonClass7:hover {
    background: linear-gradient(#bab1ba, #ededed);
  }
  #productList{
      font-size: 1.5rem;
      padding: 5% 0%;
      width:50%;
      text-align: center;
  }
  #addMed{
    width: 90%;
    height: 70vh;
    background-color: whitesmoke;
    top: 25vh;
    left:5%;
    position: fixed;

    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    display: none;
    }
    #medhold{
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      width: 90%;
      background-color: yellow;
      padding: 10px;
     margin: 0 auto;
     margin-top: 5px;

    }
    #hold{
      width: 100%;
      overflow-y: auto;
      overflow-x: hidden;
    }
    #minus,#plus{
      cursor: pointer;
    }
    .edit{
      color: red;
    }
    
    #ordset{
      padding: 2%;
      max-height: 30vh;
      overflow-x: hidden;
      overflow-y: auto;
      width:80%;
      box-shadow: 2px 2px 9px black;
    }
    #Nameing,#mobile{
      font-size:1rem;
      font-weight: 600;
      letter-spacing: 2px; 
    }